@use "../../root";

.counter {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding-top: 10px;
  border-radius: 10px;
  background-color: var(--background-accents);
  -webkit-box-shadow: 0px 8px 28px -4px rgb(146, 147, 153);
  -moz-box-shadow: 0px 8px 28px -4px rgba(146, 147, 153);
  box-shadow: 0px 8px 28px -4px rgba(146, 147, 153);

  .settings {
    display: flex;
    justify-content: end;
    width: 100%;
    margin-right: 20px;
  }

  .number {
    margin: 10px 0px 70px 0px;
    font-size: 3em;
    font-weight: 900;
    color: var(--main-color-dark);
  }
}

.settingsMenu {
  .menuElem {
    @include root.menuElem();

    border-bottom: none;
  }

  .menuElemBtn {
    @include root.menuElemButton();
  }
}
